import { Component, ViewChild } from '@angular/core';
import { Nav, Platform } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { Events } from 'ionic-angular';
import { HomePage } from '../pages/home/home';
import { ListPage } from '../pages/list/list';

import { Storage } from '@ionic/storage';
import { LoadingController } from 'ionic-angular';





@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild(Nav) nav: Nav;



  rootPage: any = '';
userDataProfile:any={};


  pages: Array<{title: string, component: any}>;

  constructor(public platform: Platform, public statusBar: StatusBar, public splashScreen: SplashScreen, public loadingCtrl: LoadingController, public storage: Storage,public events: Events) {

      this.storage.get('usr_tok_byae').then((result) => {
    console.log(result);
    if(result){
    this.rootPage = 'CalendarioPage';
    this.userDataProfile = result;
    } else {
    this.rootPage = 'LoginPage';
    }

    });

    events.subscribe('loginOK', (data) => {
      this.userDataProfile = data;
    });



    this.initializeApp();

    // used for an example of ngFor and navigation
    this.pages = [
      { title: 'Calendario', component: 'CalendarioPage' },
      { title: 'Horario', component: 'HorarioPage' }
    ];
    

  }

  initializeApp() {
    this.platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.


      this.statusBar.styleDefault();
      this.splashScreen.hide();
    });
  }



  cerrarSesion() {

        this.userDataProfile = {};
        this.storage.set(`usr_tok_byae`, false);
        this.nav.setRoot('LoginPage');

  }


  openPage(page) {
    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
    this.nav.setRoot(page);
  }
}



